<template>
  <div id="km_FAQs">
    <navigationBar />
    <section class="hero-section">
      <div class="overlay-bg"></div>
      <div class="content">
        <div class="row">
          <div class="col-sm-6 mx-auto">
            <h4 class="sub-titles">How can we help you?</h4>

            <div class="input-group mb-3">
              <input type="text" v-model="keywords" id="search_bar" class="form-control" placeholder="Type keywords to find answers" @keyup.prevent="typing()">
              <div class="input-group-append">
                <button class="btn" type="submit" @click.prevent="!hasTyped ? '' : clearSearch()"><i :class="hasTyped ? 'fa fa-times': 'fa fa-search'" class="fa-2x"></i></button>
              </div>
            </div>

            <p class="mt-5 text-center">You can also browse the questions.</p>
          </div>
        </div>
      </div>
    </section>

    <section class="questions-answers">
      <div class="container pt-5 pb-5">
        <div class="section-header">
          <span>Understand us better</span>
          <h1>Frequently Asked Questions</h1>
        </div>

        <ul class="list-unstyled" v-if="faqArray.length > 0">
          <li class="faq-item w3-card" v-for='{id, question, answer} in faqArray' :key="id">
            <button type="button" @click.prevent="toggleCollapse(id)" data-toggle="collapse" :data-target="`#quest_${id}`">{{question}} <span class="w3-right"><i class="fa fa-plus"></i></span></button>
            <div :id="`quest_${id}`" class="collapse item-content">
              {{answer}}
            </div>
          </li>
        </ul>
      </div>
    </section>
  </div>
</template>

<script>
  import navigationBar from "./fragments/navigation";
  import { mapState, mapActions } from "vuex";
  import $ from "jquery";
  export default {
    data() {
      return {
        hasTyped: false,
        keywords: "",
        faqArray: [],
      };
    },
    components: {
      navigationBar,
    },
    computed: {
      ...mapState(["faqs", "searchedFaqs"]),
    },
    methods: {
      ...mapActions(["searchFaq"]),
      typing() {
        /*eslint no-undef: 0*/
        var keywords = this.keywords;
        if (keywords.length > 0) {
          this.hasTyped = true;
          this.searchFaq(this.keywords);
          setTimeout(() => {
            this.faqArray = this.searchedFaqs;
          }, 1000);
        } else {
          this.hasTyped = false;
          this.keywords = "";
          this.faqArray = this.faqs;
        }
      },
      clearSearch() {
        this.keywords = "";
        this.hasTyped = false;
        this.faqArray = this.faqs;
      },
      toggleCollapse(id) {
        $(`#quest_${id}`).on("shown.bs.collapse", () => {
          $(`#quest_${id}`)
            .closest("li")
            .find("i")[0]
            .setAttribute("class", "fa fa-minus");
        });
        $(`#quest_${id}`).on("hidden.bs.collapse", () => {
          $(`#quest_${id}`)
            .closest("li")
            .find("i")[0]
            .setAttribute("class", "fa fa-plus");
        });
      },
    },
    mounted() {
      this.faqArray = this.faqs;
    },
  };
</script>