<template>
  <div class="content">
    <div class="section-header">
      <span>Get in touch with us</span>
      <h1>Contact Us</h1>
    </div>
    <div class="row">
      <div class="col-sm-6">
        <form @submit.prevent="submitEmail()">
          <div class="form-group">
            <input type="text" v-model="form.name" placeholder="Fullname (e.g. John Doe)" />
          </div>
          <div class="form-group">
            <input type="email" v-model="form.email" placeholder="Email" />
          </div>
          <div class="form-group">
            <input type="number" v-model="form.phone_number" placeholder="Phone" />
          </div>
          <div class="form-group">
            <textarea name="" id="" v-model="form.message" cols="20" rows="10" class="message" placeholder="Message"></textarea>
          </div>
          <div class="form-group">
            <button type="submit" class="km-btn">Send Message</button>
          </div>
        </form>
      </div>
      <div class="col-sm-6">
        <div class="km-grouping mb-4">
          <h4 class="sub-title"><i class="fa fa-phone" style="font-size: 16px; color:#b32530"></i>&nbsp;Phone</h4>
          <span>+27 00 000 0000</span>
        </div>
        <div class="km-grouping mb-4">
          <h4 class="sub-title"><i class="fa fa-envelope" style="font-size: 16px; color:#b32530"></i>&nbsp;Email</h4>
          <span>info@kmcleaning.com</span>
        </div>
        <div class="km-grouping mb-4">
          <h4 class="sub-title"><i class="fa fa-map-marker-alt" style="font-size: 16px; color:#b32530"></i>&nbsp;Address</h4>
          <span>9 Prestwich Avenue, Mthatha, 5100</span>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import { mapActions } from "vuex";
  export default {
    data() {
      return {
        error: "",
        /*eslint no-undef: 0*/
        form: new Form({
          name: "",
          email: "",
          phone_number: "",
          message: "",
        }),
      };
    },
    methods: {
      ...mapActions(["sendEmail"]),
      submitEmail() {
        if (!this.validateForm()) {
          console.log(this.error);
          return;
        }
        this.sendEmail(this.form)
          .then((data) => {
            console.log(data);
          })
          .catch((err) => {
            console.error(err);
          });
      },
      validateForm() {
        let form = this.form;
        if (form.name == "" && form.name.length < 2) {
          this.error = "Full name is required";
          return false;
        }

        if (form.email == "") {
          this.error = "Email is required";
          return false;
        }

        if (this.validateEmail(form.email)) {
          this.error = "Invalid email address";
          return false;
        }

        if (form.phone_number == "") {
          this.error = "Phone number is required";
          return false;
        }

        if (isNaN(form.phone_number)) {
          this.error = "Phone number must be a number";
          return false;
        }

        if (form.message.length < 10) {
          this.error = "Message should be at least 10 characters";
          return false;
        }

        return true;
      },
      validateEmail(email) {
        /*eslint no-useless-escape: 0*/
        const re = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
        return re.test(String(email).toLowerCase());
      },
    },
  };
</script>

