import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
import $ from 'jquery'
import 'bootstrap'
import 'popper.js'
import './assets/scss/app.scss'
import { Form } from 'vform'
window.Form = Form

Vue.config.productionTip = false


new Vue({
  render: h => h( App ),
  router,
  store
}).$mount('#app')

  $(window).scroll(function() {    
  var scroll = $(window).scrollTop();

    if ( scroll >= 200 ) {
      $( "#km_navbar" ).addClass( "navbar-add-color" );
      $("#km_navbar").addClass("shadow-sm");
  } else {
      $( "#km_navbar" ).removeClass( "navbar-add-color" );
      $("#km_navbar").removeClass("shadow-sm");
  }
  });